.empty-list {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-gap: 20px;
  justify-items: center;
}

@media (max-width: 690px) {
  .card-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-evenly;
  }
}
